import React, {Component} from "react";
import './IEEESBAffiliates.css'
import Row from "react-bootstrap/cjs/Row";

import ieeeLogo from './../../resources/partners/ieee.webp'
import ieeeRasLogo from './../../resources/partners/ieee-ras.webp'
import ieeeWieLogo from './../../resources/partners/ieee-wie.webp'
import ihuLogo from './../../resources/partners/ihu.webp'
import Col from "react-bootstrap/cjs/Col";

class IEEESBAffiliates extends Component {
    render() {
        const partners = [
            {
                "title": "IEEE",
                "url": "https://ieee.org",
                "logo": ieeeLogo
            },
            {
                "title": "IEEE WIE",
                "url": "https://wie.ieee.org",
                "logo": ieeeWieLogo
            },
            {
                "title": "IEEE RAS",
                "url": "https://ieee-ras.org",
                "logo": ieeeRasLogo
            },
            {
                "title": "IHU",
                "url": "https://ihu.gr",
                "logo": ihuLogo
            },
        ]

        return (
            <div className="affiliates">
                <Row>
                    {
                        partners.map(partner => {
                            const {title, url, logo} = partner;

                            return (
                                <Col sm className="affiliate">
                                    <a href={url}>
                                        <img src={logo} alt={title}/>
                                    </a>
                                </Col>
                            )
                        })
                    }
                </Row>
            </div>
        );
    }
}

export default IEEESBAffiliates;
